<template>
    <div class="visualizer-page">

        <div class="processes">
            
            <div class="get-item polo-get">
                <figure class="view-item">
                    <img class="image-item" :src="path_image" alt="polo">
                </figure>
                <p style="text-align: center; font-weight: 500; font-size: 30px;">{{name_item}}</p>
            </div>


            <div class="get-image person-get">

                <figure class="view-item-person">

                    <canvas class="image-item-person-s" id="imageCanvas" ref="imageCanvas"></canvas>
                </figure>

                <div class="actions-btn">

                    <form id="form-visualizer">

                        <div class="mb-3 ms mx-5">
                            <label for="formFile01" class="form-label">Subir una foto Persona</label>
                            <input class="form-control" id="imageLoader"  name="file_person" type="file" @change="updateCanvasImage">
                        </div>

                        <button type="button" class="btn-upload btn btn-primary" @click="sendFilesStep02()">Enviar</button>

                    </form>


                </div>

            </div>

            <div class="result-image result-get">
                <figure class="view-item-result">
                    <!-- <img :src="getImgUrl()" class="image-item" id="imageFinal" > -->
                    <img src = "" class="img-result" id="imageFinal">
                </figure>   

                <div v-if="arrivedImage">
                    <h2 class="title-icons-GD">¿Cómo calificarías el resultado?</h2>
                    <figure>
                        <img  class="icons-GD" src="../assets/iconos-gd.png" alt="iconos-ref" usemap="#workmap">
                        <map name="workmap">
                            <area class="icon-reaction" shape="circle" coords="50,30,20" alt="Happy01" @click="resultGrade(1)" >
                            <area class="icon-reaction" shape="circle" coords="110,30,20" alt="Happy02" @click="resultGrade(2)" >
                            <area class="icon-reaction" shape="circle" coords="50,87,20" alt="Normal01" @click="resultGrade(3)" >
                            <area class="icon-reaction" shape="circle" coords="110,87,20" alt="Normal02" @click="resultGrade(4)" >
                            <area class="icon-reaction" shape="circle" coords="50,144,20" alt="Angry01" @click="resultGrade(5)" >
                            <area class="icon-reaction" shape="circle" coords="110,144,20" alt="Angry02" @click="resultGrade(6)" >
                        </map>
                    </figure>
                </div>

            </div>

        </div>
    </div>

</template>

<script>
import axios from 'axios'
import Swal from 'sweetalert2';
import base from '@/mixins/base'

export default{
    name: "MyVisualizer",
    mixins: [base],
    data(){
        return {
            path_image: "",
            name_item: "",
            price_item: "",
            stock_item: "",
            url_colab: "",
            url_dat: "",
            url_download: "",
            path_generate_image: "personTest01.jpeg",
            path_image_load: "",
            isProcessing: false,
            itemId: "",
            arrivedImage: false,
        }
    },
    mounted(){
        this.getUrlServerColab();
        this.getInfo();
        this.getImgUrl();
    },
    methods: {
        incrCount(){
            console.log("listo incr,grade")
        },
        resultGrade(id){

            var metrics = localStorage.getItem("metricas");
            if(metrics != null){
                metrics = JSON.parse(metrics);
                if('gradesVis' in metrics){
                    metrics["gradesVis"].push(id);
                }else{
                    metrics["gradesVis"] = [id];
                }
                localStorage.setItem("metricas",JSON.stringify(metrics));
            }else{
                var obj = { "gradesVis":[id]}
                localStorage.setItem("metricas",JSON.stringify(obj));
            }

            this.arrivedImage = false;

        },
        getUrlServerColab(){
            axios
                .get('/api/recovery/url_Google')
                .then(res => {
                    console.log(res.data);
                    const data = res.data;
                    this.url_colab = data.url;
                })
                .catch(error => {
                    console.log(error);
                })
        },
        getImgUrl() {
            const img_url= this.path_generate_image;
            //console.log("actuliza image:",this.path_generate_image);
            var images = require.context('../testImages/', false, /\.(jpg|png|jpeg)$/)
            return images('./' + img_url)
        },
        changeImage(){
            var img_tag = document.getElementsByClassName("image-item-person");
            console.log(img_tag)
        },
        updateCanvasImage(e) {
            var self = this;
            var reader, files = e.target.files;
            reader = new FileReader();
            
            reader.onload = () => {
                var img = new Image();
                img.onload = function() {
                    self.drawCanvasImage(img)
                }
                img.src = (event).target.result;
            };
            reader.readAsDataURL(files[0]);
            
        },
        drawCanvasImage(img) {
            var canvas = this.$refs.imageCanvas;
            console.log("medidas:",img.width,"::",img.height);
            canvas.width = img.width;
            canvas.height = img.height;
            var ctx = canvas.getContext('2d');
            ctx.drawImage(img,0,0);
        },
        updateImage(img=""){
            console.log("actualizando");
            console.log(img);
            if(img.length == 0) this.path_generate_image = "personTest01.jpeg";
            else this.path_generate_image = img;
        },
        getImgLoadUrl() {
            const img_url= this.path_image_load;
            var images = require.context('../testImages/', false, /\.jpg$/)
            return images('./' + img_url)
        },
        init(){
            if("mediaDevices" in navigator && "getUserMedia" in navigator.mediaDevices){
                navigator.mediaDevices.getUserMedia({video: true}).then(stream =>{
                    const videoPlayer = document.querySelector("video");
                    videoPlayer.srcObject = stream;
                    videoPlayer.play();
                })
            }
        },
        takePicture(){
            const picture = document.querySelector("canvas");
            const ctx = picture.getContext("2d");
            ctx.imageSmoothingEnabled = true;
            ctx.imageSmoothingQuality = "high";
            ctx.drawImage(document.querySelector("video"),0,0,picture.width,picture.height);
        },
        getInfo(){
            const item_id= this.$route.params.id;
            this.itemId = this.$route.params.id;

            axios
                .get('/api/paginaItem/detailsPolo/'+item_id)
                .then(res => {
                    //console.log(res.data);
                    const data = res.data;
                    this.path_image = data.path_image;
                    this.name_item = data.name_modelo;
                    this.price_item = data.price;
                    this.stock_item = data.stock;
                })
                .catch(error => {
                    console.log(error);
                })
            
            //console.log("error no hay");
                
        },
        prub(){
            console.log("gaaaaa");

            let timerInterval
            Swal.fire({
                title: 'Espere...',
                html: 'El proceso terminará en <b></b> millisegundos.',
                timer: 2000,
                timerProgressBar: true,
                didOpen: () => {
                    Swal.showLoading()
                    const b = Swal.getHtmlContainer().querySelector('b')
                    timerInterval = setInterval(() => {
                    b.textContent = Swal.getTimerLeft()
                    }, 100)
                },
                willClose: () => {
                    clearInterval(timerInterval)
                }
            })

            console.log("RAAAAAAAA");
            console.log(1+1);
            const a="nose";
            for(var i=0;i<a.length;i++) console.log(a[i]);

        },
        sendFilesStep02(){

            /*

            var metrics = localStorage.getItem("metricas");
            if(metrics != null){
                metrics = JSON.parse(metrics);
                if('counterVis' in metrics){
                    metrics["counterVis"] = metrics["counterVis"] + 1;
                }else{
                    metrics["counterVis"] = 1;
                }
                localStorage.setItem("metricas",JSON.stringify(metrics));
            }else{
                var obj = { "counterVis": 1}
                localStorage.setItem("metricas",JSON.stringify(obj));
            }

            */



            const form = document.getElementById("form-visualizer");
            const fileData = new FormData(form);
            fileData.append("id_polo", this.itemId);
            
   
            /*
            for (var pair of fileData.entries()) {
                console.log(pair[0]+ ', ' + pair[1]); 
            }*/

            //var url_bugha =  { 'url_persona': 'https://anonfiles.com/X742I8p0y4/file_person_png' , 'url_polo': 'https://anonfiles.com/d257I8p7y0/file_polo_png' };

            const axiosInstance = axios.create({
                headers: {
                    "Access-Control-Allow-Origin": "*",
                    "Access-Control-Allow-Credentials": "true",
                    "Access-Control-Allow-Methods": "*",
                    "Access-Control-Allow-Headers": "Origin, X-Requested-With, Content-Type, Accept, Authorization"
                }
            });

           let timerInterval
            Swal.fire({
                title: 'Espere...',
                html: 'El proceso terminará en unos segundos.',
                //html: 'El proceso terminará en <b></b> milisegundos.',
                timer: 40000,
                timerProgressBar: true,
                didOpen: () => {
                    Swal.showLoading()
                    const b = Swal.getHtmlContainer().querySelector('b')
                    timerInterval = setInterval(() => {
                    b.textContent = Swal.getTimerLeft();

                    }, 100)
                },
                willClose: () => {
                    clearInterval(timerInterval)
                }
            })

            axiosInstance
                .post('/api/probador/',fileData,{
                    headers: { 
                        "Content-Type": "multipart/form-data",
                    },
                })
                .then(response =>{
                    console.log("download colab msg: ",response);
                    var tag_img = document.getElementById("imageFinal");
                    tag_img.src = response.data.img;
                    this.arrivedImage = true;

                   /*
                    // console.log("ARCHIVO SUBIDO COLAB");
                    // var responseBlob = new Blob([response.data], {type:"image/png"}); 
                    // var reader = new window.FileReader();
                    // reader.readAsDataURL(responseBlob); 
                    // console.log(reader);
                    // console.log("Fin del READER");
                   
                    // reader.onload = function() {
                    //     var imageDataUrl = reader.result;
                    //     console.log(imageDataUrl);
                    //     console.log("Fin del image");
                    //     // imageDataUrl = imageDataUrl.replace("b&#39;", "") 
                    //     // imageDataUrl = imageDataUrl.replace("&#39;", "") 

                    //     var tag_img = document.getElementById("imageFinal");
                    //     tag_img.src = imageDataUrl;
                    //     console.log("SRC completado");
                    // }
                    */
                    
                    
                    }).catch(error => {
                    console.log(error)
                })

            
                
        },
    },

}
</script>

<style scoped>

