    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 1fr;
    grid-column-gap: 35px;
    grid-row-gap: 0px;
    padding-top: 20px;
}



/*Start - Columna Polo*/ 

.view-item , .image-item{
    width: 300px;
    margin: auto;
}

.view-item{
    margin-top: 20px;
}

.image-item{
    display: block;
    margin: auto;
    min-width: 270px;
    max-height: 300px !important;

}

.polo-get{
    background-color: skyblue;
    height: 400px;
    width: 100%;
    max-width: 350px;
    margin-left: 20px;
    margin-top: 25px;
}

/*End - Columna Polo*/


/*Start - Columna Foto Usuario*/ 

