    margin: auto;
    margin-bottom: 20px;
    padding: 10px 0;
    margin-top: 10px;
    margin-right: 20px;
    z-index: -2;
}

.image-item-person{
    display: block;
    width: 170px;
    margin: auto;
}

canvas{
    width: 300px;
