}

.btn-result{
    width: 50%;
    margin-top: 20px;
    margin-left: 25%;
}

.processing{
    font-weight: bold;
    margin-top: 40px;
    text-align: center;
}

/*End - Columna Foto Usuario*/


/*Start - Columna Resultado*/ 

.no-items-found-active{
    display: block;
}

