@media only screen and (max-width: 1050px){
    .processes{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .polo-get{
        height: 400px;
        padding-top: 20px;
        margin-bottom: 50px;
    }

    .person-get{
        margin-top: 0;
        padding-top: 0;
    }

    .result-get{
        width: 100%;
        max-width: 550px;
        margin-top: 0;
        padding-top: 0;
        padding-bottom: 100px;
    }

    canvas{
        width: 90%;
        max-width: 300px;
        max-height: 400px;
    }

    .view-item-result{
        display: block;
        margin: auto;
        width: 90%;
        height: 500px;
        margin: auto;
        margin-bottom: 20px;
        padding: 10px 0;
        margin-top: 10px;
        margin-right: 20px;
        z-index: -2;
    }

    .view-item{
        margin-top: 0;
    }

    .image-item{
        min-width: 270px;
        max-height: 300px !important;
    }

    .img-result{
        width: 90%;
        max-width: 300px !important;
    }


